import React, { FC, useCallback, useState, useRef, useMemo, useEffect } from 'react';
import { Link, history } from 'umi';
import { Form, Row, Col, Popconfirm, Tooltip, Input, Radio, DatePicker, Select, Button, message, Modal } from 'antd';
import { Prompt } from 'react-router-dom';
// import {  } from './services';
import { Role, getAuthority } from '@/utils/authority';
import { withAuthorized } from '@/utils/Authorized';
import ChunkTitle from '@/components/chunkTitle';
import style from './index.less';
import { ProductionClassMaps } from '@/utils/constant.js';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import { MapItem } from '@/types/types';
import { OnlinePlantformMaps } from './constant';
import classnames from 'classnames';
import ProductionDetail from '@/components/ProductionDetail';
import InitCountSetModal from './components/InitCountSetModal';
import { createProduction, updateProduction } from './services';
import { useGetProductionDetail, productionOnOrOutLine } from '@/services/publicServices';
import { searchDramaContent } from '@/components/DramaContentSearch/services';
import moment, { Moment } from 'moment';
import UploadFile from '@/components/UploadFile';
import { Online, Outline } from '@/components/miniComponents';
import { ProducionInfo } from './types';

const sizeOptions = [
  {
    width: 1080,
    height: 1512,
    text: '海报'
  }
];
const layout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 20 },
};
const halfLayout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
};

const { TextArea } = Input;

const authority = getAuthority();
const isSystem = authority.indexOf(Role.SYSTEM) >= 0;

const ProductionDataForm = (props: any) => {
  const [form] = Form.useForm();
  const [preData, setPreData] = useState();
  const [initCountVisible, setInitCountVisible] = useState(false);
  const [publicScoreDistribution, setPublicScoreDistribution] = useState();
  const [publicScore, setPublicScore] = useState({ publicScore: 0, publicScorePeople: 0 });
  const [btnLoading, setBtnLoading] = useState(false);

  const leaveLockRef = useRef(true);
  const [leaveConfirm, setLeaveConfirm] = useState(false);

  const productionId = useMemo(() => {
    return props?.location?.query?.dramaId;
  }, [props]);

  const { productionDetail, detailLoading } = useGetProductionDetail(productionId);

  const isOnLine = useMemo(() => {
    return productionDetail?.onlineStatus === 1;
  }, [productionDetail?.onlineStatus]);

  const handleSetFormDataInit = () => {
    const { title, coverImage, description, category, platform, showTime: showTimeData, topDeadline, finishTime } = productionDetail;
    const imgList = (coverImage ? JSON.parse(coverImage) : []);

    form.setFieldsValue({
      title,
      coverImage: (
        imgList?.length
          ? [{
            uid: new Date().getTime(),
            name: new Date().getTime(),
            status: 'done',
            url: imgList[0],
          }]
          : []),
      description,
      category,
      platform: (platform ? JSON.parse(platform) : []),
      showTime: showTimeData && moment(showTimeData),
      topDeadline: topDeadline && moment(topDeadline),
      finishTime: finishTime && moment(finishTime)
    });
    setPreData(productionDetail);
  };


  useEffect(() => {
    if (productionDetail?.dramaId) {
      handleSetFormDataInit();
    }
  }, [productionDetail?.dramaId]);

  useEffect(() => {
    const timeout = setTimeout(() => { // 15秒之后, 离开页面需要二次确认
      setLeaveConfirm(true);
    }, 15000);

    return () => {
      clearTimeout(timeout);
    };
  }, []);


  const handlePre = useCallback(() => {
    const { validateFields } = form;

    validateFields()
      .then((values: any) => {
        const preDatas = { ...values };

        preDatas.coverImage = (values?.coverImage?.length ? JSON.stringify([values?.coverImage[0]?.url]) : JSON.stringify([]));
        setPreData(preDatas);
      })
      .catch((errorInfo: any) => {
        console.log('formError', errorInfo);
      });
  }, [form]);

  const handlePublish = useCallback(() => {
    const { validateFields } = form;

    validateFields()
      .then(async (values: any) => {
        const { title, coverImage, description, category, platform, showTime: showTimeData, topDeadline, finishTime } = values;

        setBtnLoading(true);
        const params = {
          title,
          coverImage: (coverImage?.length ? [coverImage[0]?.url] : []),
          description,
          category,
          platform,
          showTime: showTimeData?.valueOf(),
          topDeadline: topDeadline?.valueOf(),
          finishTime: finishTime?.valueOf()
        };
        // 大众评分 新建--传 编辑--不传
        const publicScoreParams = {
          ...publicScore,
          publicScoreDistribution: publicScoreDistribution && JSON.stringify(publicScoreDistribution)
        };

        try {
          // 检查作品名称是否重复
          const checkRes: any = await searchDramaContent({
            termTitle: title,
            onlineStatus: undefined, // '1' 上线
            category,
          });

          if ((checkRes?.code === 200) && (checkRes?.data?.list?.length > 0)) {
            let isRepeat = false;

            if (productionId) {
              // 编辑 对比id
              isRepeat = true;
              const repeatData = checkRes?.data?.list?.find((item: ProducionInfo) => item?.title === title);

              if (repeatData?.dramaId === productionId) {
                isRepeat = false;
              }
            } else {
              // 新建 对比名称
              const index = checkRes?.data?.list?.findIndex((item: ProducionInfo) => item?.title === title);

              if (index >= 0) {
                isRepeat = true;
              }
            }
            if (isRepeat) {
              message.error('作品名称重复，请更改');

              return;
            }

          } else if (checkRes?.code !== 200) {
            message.error(checkRes?.message || '服务器出问题啦');

            return;
          }

          let res: any = {};

          if (productionId) {
            res = await updateProduction({ ...productionDetail, ...params, productionId });
            // 下线作品 重新上线
            if (!isOnLine && (res?.code === 200)) {
              res = await productionOnOrOutLine({
                dramaId: productionId,
                onlineStatus: 1
              });
            }
          } else {
            res = await createProduction({ ...params, ...publicScoreParams });
          }

          if (res?.code === 200) {
            setLeaveConfirm(false);
            message.success('操作成功！');
            history.push('/content-manage/production-manage');
          } else {
            message.error(res?.message || '服务器出问题啦');
          }
        } catch (error) {
          message.error(error?.message);
        } finally {
          setBtnLoading(false);
        }
      })
      .catch((errorInfo: any) => {
        console.log('formError', errorInfo);
      });
  }, [form, publicScoreDistribution, publicScore, productionId, isOnLine, productionDetail]);

  const handleHiddenInitCountModal = useCallback(() => {
    setInitCountVisible(false);
  }, []);
  const handleShowInitCountModal = useCallback(() => {
    setInitCountVisible(true);
  }, []);

  const handleChangeShowTime = useCallback((value: Moment) => {
    form.setFieldsValue({
      topDeadline: moment(value || undefined).add(7, 'days')
    });

    return value;
  }, []);

  const disabledShowTime = (current: Moment) => {
    const finishTimeValue = form.getFieldValue('finishTime');

    if (finishTimeValue) {
      return current && current.endOf('day').diff(finishTimeValue.endOf('day'), 'd') > -1;
    } else {
      return false;
    }

  };

  const disabledFinishTime = (current: Moment) => {
    const showTimeValue = form.getFieldValue('showTime');

    if (showTimeValue) {
      return current && current.endOf('day').diff(showTimeValue.endOf('day'), 'd') < 1;
    } else {
      return false;
    }
  };

  return (
    <div className={ style.productionDataFormWrap }>
      <InitCountSetModal visible={ initCountVisible }
        handleHidden={ handleHiddenInitCountModal }
        setPublicScoreDistribution={ setPublicScoreDistribution }
        setPublicScore={ setPublicScore }
      />
      <Prompt when={ leaveConfirm }
        message={ (location: any) => {
          if (!leaveLockRef.current) {
            return true;
          }

          Modal.confirm({
            title: '即将离开页面',
            content: '正在编辑作品, 确认离开吗',
            onOk: () => {
              leaveLockRef.current = false;
              history.push(location.pathname);
            },
            onCancel: () => {
              leaveLockRef.current = true;
            },
          });

          return !leaveLockRef.current;
        } }
      />
      <Form { ...layout } form={ form }>
        <Row>
          <Col span={ 12 } className={ style.productionDataFormChunk }>
            <Button type="primary" disabled={ productionId } className={ style.addInitCount } onClick={ handleShowInitCountModal }>添加初始评分数据</Button>
            <ChunkTitle title="作品资料管理" />
            <div className={ style.productionDetail }>
              <Form.Item label="名称"
                name="title"
                rules={ [{ required: true, message: '请输入名称!' }] }
              >
                <Input style={{ width: '80%' }} placeholder="请输入作品名称" />
              </Form.Item>
              {productionId
                ? (isOnLine
                  ? <p className={ style.productionStatus }><Online /></p>
                  : (
                    <div className={ style.productionStatus }>
                      <Tooltip placement="topRight" title={ <p style={{ width: 240 }}>下线作品点击发布后可重新在前台展示</p> }>
                        <Outline />
                        <ExclamationCircleOutlined className={ style.productionIcon } />
                      </Tooltip>
                    </div>))
                : null}
            </div>
            <Form.Item label="海报"
              name="coverImage" valuePropName="fileList"
            >
              <UploadFile sizeOptions={ sizeOptions } isSingle={ true } listType="picture-card" />
            </Form.Item>
            <Form.Item label="简介"
              name="description"
            >
              <TextArea rows={ 5 } placeholder="请输入简介" />
            </Form.Item>
            <Row>
              <Col span={ 12 }>
                <Form.Item label="品类"
                  name="category" { ...halfLayout }
                  rules={ [{ required: true, message: '请选择品类!' }] }
                >
                  <Radio.Group>
                    {ProductionClassMaps.map(({ value, label }: MapItem) => <Radio key={ value } value={ value }>{label}</Radio>)}
                  </Radio.Group>
                </Form.Item>
              </Col>
              <Col span={ 12 }>
                <Form.Item label="在播平台"
                  name="platform" { ...halfLayout }
                >
                  <Select mode="multiple" options={ OnlinePlantformMaps }>
                  </Select>
                </Form.Item>
              </Col>
            </Row>

            <Row>
              <Col span={ 12 }>
                <Form.Item label="上映时间"
                  name="showTime" { ...halfLayout }
                  rules={ [{ required: true, message: '请选择上映时间!' }] }
                  normalize={ handleChangeShowTime }
                >
                  <DatePicker disabledDate={ disabledShowTime } />
                </Form.Item>
              </Col>
              <Col span={ 12 }>
                <Form.Item label="完结时间"
                  name="finishTime" { ...halfLayout }
                  rules={ [{ required: true, message: '请选择完结时间!' }] }
                >
                  <DatePicker disabledDate={ disabledFinishTime } />
                </Form.Item>
              </Col>
            </Row>

            <Form.Item label="新片置顶时间"
              name="topDeadline"
              rules={ [{ required: true, message: '请选择新片置顶时间!' }] }
            >
              <DatePicker showTime={ true } />
            </Form.Item>


            <div className={ style.btnList }>
              <Button className={ classnames([style.btn, style.greenBtn]) } onClick={ handlePre }>
                {productionId ? '更新预览' : '预览'}
              </Button>
              <Button className={ style.btn } onClick={ handleSetFormDataInit }>重置</Button>
              <Popconfirm placement="topRight"
                title="确认 发布该作品吗"
                onConfirm={ handlePublish }
                okText="确定"
                cancelText="取消"
              >
                <Button loading={ btnLoading } className={ style.btn } type="primary">{productionId && !isOnLine ? '重新上线' : '发布'}</Button>
              </Popconfirm>
            </div>
          </Col>
          <Col span={ 12 } className={ classnames([style.productionDataFormChunk, style.commontChunkWrap]) }>
            <div className={ style.commontChunk }>
              <ChunkTitle title="效果预览" />
              <ProductionDetail production={ preData }
                imgSpan={ 8 } detailSpan={ 16 }
              />
            </div>
            {productionId && isSystem
              ? (
                <div className={ style.commontChunk }>
                  <p className={ style.commontItem }>
                    <Link to={ `/commont-manage/public-comment-manage?dramaId=${ productionId }` }>大众评论管理</Link>
                  </p>
                  <p className={ style.commontItem }>
                    <Link to={ `/commont-manage/expert-comment-manage?dramaId=${ productionId }` }>专业评论管理</Link>
                  </p>
                  <p className={ style.commontItem }>
                    <Link to={ `/data-manage/data-control?dramaId=${ productionId }` }>数据调控</Link>
                  </p>
                </div>)
              : null
            }
          </Col>
        </Row>
      </Form>
    </div>
  );
};

export default withAuthorized(ProductionDataForm, [Role.SYSTEM, Role.WORKS]);
