import useSWR from '@/hooks/useSWR';
import request from '@/utils/request';
import { requestBasePath } from '@/services/login';

// /wind-vane-backend/drama/operate/createDramaContent
import { ProducionInfo } from './types';


// 创建作品
export function createProduction(params: ProducionInfo) {
  return request(`${ requestBasePath }/wingvate/wind-vane-backend/drama/operate/createDramaContent`, {
    method: 'POST',
    body: {
      ...params,
      coverImage: JSON.stringify(params.coverImage),
      platform: JSON.stringify(params.platform),
    }
  });
}

// 更新作品 /wind-vane-backend/drama/operate/updateDramaContent
export function updateProduction(params: ProducionInfo) {
  return request(`${ requestBasePath }/wingvate/wind-vane-backend/drama/operate/updateDramaContent`, {
    method: 'POST',
    body: {
      content: {
        ...params,
        coverImage: JSON.stringify(params.coverImage),
        platform: JSON.stringify(params.platform),
      },
      dramaId: params.productionId,
    }
  });
}
