import { Pagenation } from './../../types/types.d';
import request from '@/utils/request';

import { requestBasePath } from '@/services/login';


// 获取搜索列表
export async function searchDramaContent(params: {title?: string; termTitle?: string; onlineStatus?: string; category?: string}) {
  return await request(`${ requestBasePath }/wingvate/wind-vane-backend/drama/query/queryDramaContentList`, {
    method: 'POST',
    body: {
      query: params,
      sort: { type: 'desc', key: 'createTime' },
      pagination: { pageSize: 100, current: 1 }
    }
  });
}
